*, :after, :before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    // scroll-behavior: smooth;
}

video{
    // transform: scaleX(-1);
    width: 100%;
    height: auto;
}

body {
    font-family: Arial, sans-serif;
    background-color: #f8f9fa;
    text-align: center;
}
button {
    margin: 5px;
    padding: 10px 15px;
    border: none;
    background-color: #007bff;
    color: white;
    cursor: pointer;
}
button:hover {
    background-color: #0056b3;
}
.video-feed {
    width: 100%;
    max-width: 600px;
    border-radius: 10px;
}
.filters {
    margin-top: 10px;
}
.image-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    margin-top: 20px;
}

.captured-photo {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 8px;
    border: 2px solid #000;
}

/* Countdown Timer */
.countdown {
    position: relative;
    text-align: center;
    font-size: 50px;
    font-weight: bold;
    color: red;
    margin: 20px 0;
    background: rgba(255, 255, 255, 0.8);
    padding: 10px 20px;
    border-radius: 10px;
    animation: countdown-animation 0.5s ease-in-out alternate infinite;
}

/* Photobooth Strip */
.photobooth-strip {
    width: 150px; /* Adjust as needed */
    max-width: 300px; /* Prevents it from being too large */
    height: auto; /* Keeps the aspect ratio */
    display: block;
    margin: 10px auto; /* Centers the image */
}

/* Photobooth Layout */
.photo-booth {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    text-align: center;
    padding: 10px;
}

/* Camera Container */
.camera-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    max-width: 700px;
    height: 450px;
    position: relative;
}

/* live cam*/
.video-feed {
    width: 600px;
    height: 450px;
    object-fit: cover; /* Prevents weird stretching */
    transform: scaleX(-1); /* Mirror effect */
    border-radius: 10px;
    border: 2px solid black;
}

/* Animation */
.animate {
    animation: pop 0.5s ease-in-out alternate infinite;
}

/* Photo Strip Preview */
.photo-preview {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    margin-top: 0px;
}

.photo-preview canvas {
    max-width: 320px;
    width: 100%;
    height: auto;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    margin-top: 20px;
}

.photo-preview img {
    width: 100%;
    height: auto; /* Ensures correct aspect ratio */
    max-width: 300px;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

/* Fix Photo Strip Squishing */
.photo-strip {
    display: block;
    max-width: 400px;
    width: 100%; 
    height: auto;
    margin-top: -10px;
    margin-bottom: 5px;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}


/* Fix Capture Button Placement */
.controls {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    gap: 10px;
    position: relative; /* Ensures it stays below */
    z-index: 10; /* Keeps it above other elements */
    width: 100%;
}

/* General Button Styling */
button {
    background: transparent;
    color: black;
    border: 2px solid black;
    border-radius: 20px;
    padding: 10px 15px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

button:hover {
    background: black !important;
    color: white !important;
    transform: scale(1.05);
}

/* Color Picker Options */
.color-options {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
}

/* Fix Layout Between Camera and Photo Strip */
.photo-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 30px; /* Increased spacing */
    width: 100%;
    max-width: 1200px;
    margin: 20px auto;
}

/* Fix Photo Strip Thumbnails */
.preview-side {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    min-width: 120px;
    margin-left: 20px;
    margin-top: -120px; /* Adjust this value to raise the first photo */
}


.side-preview {
    width: 160px;
    height: 120px;
    object-fit: cover; /* Ensure image fills the space */
    border-radius: 8px;
    border: 2px solid #000;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
}


/* Hide Captured Image Under Camera */
.hidden {
    display: none !important;
}

/* Footer Styling */
footer {
    display: block;
    padding: 10px;
    background: transparent;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    margin-top: 20px;
}

/* Footer Link */
footer a {
    color: pink;
    text-decoration: underline;
    font-weight: bold;
    font-size: 16px;
    border: 2px solid pink;
    border-radius: 20px;
    padding: 5px 15px;
    transition: color 0.3s ease-in-out;
}

/* Hover Effect */
footer a:hover {
    text-decoration: underline;
}

/* Ensure the Footer Stays at the Bottom */
.home-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
    padding: 20px;
}

/* Fix Horizontal Scrolling Issue */
html, body {
    overflow-x: hidden;
    max-width: 100%;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
    .camera-container {
        width: 100%;
        height: 300px; /* Adjust height */
    }

    .video-feed {
        width: 100%;
        max-width: 600px;
        height: auto;
        aspect-ratio: 4/3; /* Lock to 4:3 aspect ratio */
        object-fit: cover;
        transform: scaleX(-1); /* Mirror effect */
        border-radius: 10px;
        border: 2px solid black;
    }

    .controls, .filters {
        flex-wrap: wrap;
        justify-content: center;
    }

    .controls button {
        width: 90%;
        padding: 12px;
        font-size: 16px;
    }
}

/* Animations */
@keyframes pop {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.2); opacity: 0.8; }
}

@keyframes countdown-animation {
    from { opacity: 1; }
    to { opacity: 0.5; }
}  
